.home-div{
    height: 550px;
    display: block;
}

.h2-hello{
    color: white;
    margin-top: 200px;
    line-height: 45px;
    display: inline-block;
}

.burger-image-div{
    float: right;
    height: 100%;
}

.home-text-div{
    float: left;
}

.create-button {
    font-size: 16px;
    display: block;
    font-weight: bold;
    margin-top: 20px;
    margin-left: 180px;
    border-style: solid;
    width: 150px;
    height: 50px;
    border-radius: 25px;
    font-family: "Pangolin";
    text-decoration: none;
    color: black;
    background-color: white;
    cursor: pointer;
    transition: background-color 0.15s;
}

.create-button:hover {
    background-color: yellow;
}

.meal-div{
    margin-top: 0px;
    display: block;
    width: 100%;
}
.what-we-serve{
    text-align: center;
    display: block;
    margin-bottom: 20px;
}

.meal-card{
    width: 23.4%;
    margin-bottom: 10px;
    display: inline-block;
    margin-right: 20px;
}

.meal-name{
    margin-bottom: 2px;
    letter-spacing: 1px;
    display: inline-block;
}
.meal-price{
    float: right;
    margin-bottom: 2px;
    display: inline-block;
    margin-left: 50px;
}

.meal-image{
    width: 100%;
    height: 200px;
    border-radius: 10px;
}